import React, { Component } from 'react';

class ProfileItem extends Component {
    render() {
        return(
            <React.Fragment>
                <div id="profileItem">
                    {/* <p>{this.props.messages[this.props.id].text}</p> */}
                    <p>{this.props.messages[this.props.index].text}<button className="profileBtn" type="button"><img id='deleteButton' src="data:image/jpeg;base64,"></img></button></p>
                    
                </div>
            </React.Fragment>
        )
    }
}

export default ProfileItem;


